.poster-image{
    height: 600px;
}

.poster-image > img {
    margin: auto;
    display: block;
    width: 100%;
}

.poster-image-over {
    position: absolute;
    padding: 5rem;
    bottom: 0px;
    height: 70%;
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: flex-end;
    align-items: flex-start;
    background-image: linear-gradient(rgb(0,0,0,0), rgb(0,0,0,1));
    opacity: .3;
    transition: opacity .3s ease-out;
    -webkit-transition: opacity .3s ease-out;
    -moz-transition: opacity .3s ease-out;
    -ms-transition: opacity .3s ease-out;
    -o-transition: opacity .3s ease-out;
}   

.poster-image-over:hover {
    opacity: 1;
}

.poster-title {
    font-weight: 900;
    font-size: 4rem;
    margin-bottom: 0.4rem;
    text-align: left;
}

.poster-runtime {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.poster-rating {
    margin-left: 3rem;
}

.poster-description {
    font-style: italic;
    font-size: 1rem;
    margin-bottom: 0.25rem;
    display: flex;
    text-align: left;
    width: 50%;
}